import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { connect } from 'react-redux'
import {
  CBadge,
  CDropdown,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
  CImg
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { userLogout } from './../actions/user'
import { Redirect } from 'react-router-dom'

const TheHeaderDropdown = (props) => {
  

 const logout = () =>{
     props.userLogout()
 }

 const profile = () =>{
    return <Redirect to='/user' />
}

  return (
    <CDropdown
      inNav
      className="c-header-nav-items mx-2"
      direction="down"
    >
      <CDropdownToggle className="c-header-nav-link" caret={false}>
        <div className="c-avatar">

          <CImg
            src={process.env.REACT_APP_SERVER_URL+ props.user.user.photo} 
            className="c-avatar-img"
            alt={props.username}
            onError={(e)=>{e.target.onerror=null; e.target.src='icons/profile_4.png'} }
          />
        </div>
      </CDropdownToggle>
      <CDropdownMenu className="pt-0" placement="bottom-end">
        <CDropdownItem
          header
          tag="div"
          color="light"
          className="text-center"
        >
          <strong>Account</strong>
        </CDropdownItem>
       <CDropdownItem onClick={profile}>
          <CIcon name="cil-user" className="mfe-2" />Profile
        </CDropdownItem>
        <CDropdownItem divider />
        <CDropdownItem onClick={logout}>
          <CIcon name="cil-lock-locked" className="mfe-2" /> 
          Log Out
        </CDropdownItem>
      </CDropdownMenu>
    </CDropdown>
  )
}
const mapStateToProps =(state)=>({
user:state.userReducer
})
export default connect(mapStateToProps, {userLogout})(TheHeaderDropdown)
